// Open Sans
@font-face {
    font-family: 'Open Sans';
    font-weight: 300;
    font-style: normal;
    src: url('@{font-path}/Open-Sans/Open-Sans-300/Open-Sans-300.eot');
    src: url('@{font-path}/Open-Sans/Open-Sans-300/Open-Sans-300.eot?#iefix') format('embedded-opentype'),
        local('Open Sans Light'),
        local('Open-Sans-300'),
        url('@{font-path}/Open-Sans/Open-Sans-300/Open-Sans-300.woff2') format('woff2'),
        url('@{font-path}/Open-Sans/Open-Sans-300/Open-Sans-300.woff') format('woff'),
        url('@{font-path}/Open-Sans/Open-Sans-300/Open-Sans-300.ttf') format('truetype'),
        url('@{font-path}/Open-Sans/Open-Sans-300/Open-Sans-300.svg#OpenSans') format('svg');
}
